import React, { Component } from 'react';
import { Carousel } from 'react-responsive-carousel';

import "react-responsive-carousel/lib/styles/carousel.min.css";
import './Gallery.css';

import sl1 from '../../assets/img/slide_1.jpg';
import sl2 from '../../assets/img/slide_2.jpg';
import sl3 from '../../assets/img/slide_3.jpg';
import sl4 from '../../assets/img/slide_4.jpg';

class Gallery extends Component {
	render() {
		return (
			<div className="GalleryWrapper">
				<div className="GalleryImageWrapper">
					<Carousel autoPlay infiniteLoop showArrows={false} showStatus={false} showIndicators={false} showThumbs={false} swipeable={false}>
						<div><img src={sl1} alt=""></img></div>
						<div><img src={sl2} alt=""></img></div>
						<div><img src={sl3} alt=""></img></div>
						<div><img src={sl4} alt=""></img></div>
					</Carousel>
				</div>
			</div>
		);
	}
}

export default Gallery;